.tabs {
    @include grid(3, 2rem);
    button {
        background-color: $white;
        font-size: 1.4rem;
        font-weight: $bold;
        padding: 1rem;
        text-align: center;
        text-transform: uppercase;
        border: none;

        &.actual{
            background-color: $blue;
            color: $white;
        }
        &:hover {
            cursor: pointer;
        }
    }
}
